import { Button, Input, Select, Table } from 'antd'
import { useState } from 'react'

const dataSource = [
	{
		key: '1',
		name: '',
		age: 32,
		address: '10 Downing Street'
	},
	{
		key: '2',
		name: '',
		age: 42,
		address: '10 Downing Street'
	}
]

const columns = [
	{
		title: '',
		dataIndex: 'name',
		key: 'name'
	},
	{
		title: '',
		dataIndex: 'age',
		key: 'age'
	},
	{
		title: '',
		dataIndex: 'address',
		key: 'address'
	}
]

const optionsForSelector = [
	{ value: 'moscow', label: '' },
	{ value: 'samara', label: '' },
	{ value: 'sochi', label: '' },
	{ value: 'secretCity', label: ' ', disabled: true }
]

function SandBox() {
	const [counter, setCounter] = useState(0)
	const [city, setCity] = useState('moscow')
	const [inputVal, setInputVal] = useState('')

	function onButtonClick() {
		setCounter(counter + 1)
	}

	function onSelectorChange(value) {
		setCity(value)
	}

	function onInputChange(event) {
		setInputVal(event.target.value)
	}

	return (
		<>
			<div style={{ marginBottom: 20 }}>
				<span> : </span>
				<Button onClick={onButtonClick}>{'   ' + (counter === 0 ? '' : ' :)')}</Button>
				<span>  = {counter}</span>
			</div>
			<div style={{ marginBottom: 20 }}>
				<span> : </span>
				<Select style={{ width: 150 }} options={optionsForSelector} value={city} onChange={onSelectorChange} />
				&nbsp;
				<span>   {optionsForSelector.find(c => c.value == city).label}?</span>
			</div>
			<div style={{ marginBottom: 20 }}>
				<span> : </span>
				<Input placeholder=' ...' style={{ width: 150 }} onChange={onInputChange} />
				&nbsp;
				<span>{inputVal}</span>
			</div>
			<div>
				<span> : </span>
				<Table dataSource={dataSource} columns={columns} />
			</div>
		</>
	)
}

export default SandBox

